header {
  position: sticky;
  width: 100%;
  height: 12.5vh;
  top: 0;
  background-color: #98ddca;
  /* font-family: "Roboto Condensed", sans-serif; */
  padding-top: 1em;
  box-shadow: 0 -3px 10px 5px rgb(154, 154, 154);
  z-index: 1;
}

.search {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  margin-bottom: 0.95rem;
  width: 28vw;
  height: 5vh;
  border-radius: 40px;
  background: white;
}

.search > input {
  width: 21vw;
  height: 100%;
  font-size: 90%;
  margin-right: 0.5vw;
  outline: none;
  border: none;
  background-color: unset;
}

.search > button {
  border: none;
  background: unset;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.search + section {
  display: flex;
  justify-content: center;
  height: 33.5px;
}

header > section > label {
  display: flex;
  align-items: center;
  justify-content: center;
}

section > select {
  width: 8vw;
  margin-right: 1.3vw;
  text-align: center;
  border: none;
  border-radius: 0 0.7rem 0.7rem 0;
  cursor: pointer;
  outline: none;
}

#order {
  width: 4vw;
}

.filterIcon {
  padding-left: 0.3vw;
  border-radius: 0.7rem 0 0 0.7rem;
  background-color: white;
}

.orderIcon {
  transform: rotate(90deg);
  position: relative;
  width: 33.5px;
  border-radius: 0 0 0.7rem 0.7rem;
  background-color: white;
}

.orderIcon + select + button {
  width: 3.5vw;
  font-size: 90%;
  border: solid 0.2rem #ffd3b4;
  border-radius: 0.6rem;
  margin-left: 1vw;
  background-color: white;
  cursor: pointer;
}

.orderIcon + select + button:hover {
  background-color: #ffd3b4;
  border-color: white;
}
